.modal-gift {
    z-index: 10000;
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.5);
    opacity: 0; 
    transform: scale(0.9); 
    pointer-events: none; 
    transition: opacity 0.3s ease, transform 0.3s ease;
}

.modal-gift.fade-in {
    opacity: 1; /* Плавно появляется */
    transform: scale(1); /* Возвращается к нормальному размеру */
    pointer-events: auto; /* Включаем клики */
}

.modal-gift.fade-out {
    opacity: 0; /* Плавно исчезает */
    transform: scale(0.9); /* Уменьшается при исчезновении */
    pointer-events: none; /* Отключаем клики */
}

.block-modal-info-gift {
    color: white;
    height: auto;
    width: 77%;
    max-width: 400px;
    background: rgb(73, 0, 60);
    background: linear-gradient(175deg, rgba(73, 0, 60) 0%, rgba(55, 0, 45) 25%, rgba(0, 0, 0) 58%);
    border-radius: 20px;
    position: relative;
    text-align: center;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.5);
}

.close-icon-modal-gift {
    width: 25px;
    height: 25px;
    position: absolute;
    top: 15px;
    right: 15px;
    cursor: pointer;
}

.gift-icon-modal {
    width: 150px;
    height: 150px;
    margin-bottom: 15px;
}

.text-info-about-gift {
    margin: 15px 0;
    font-family: 'Inter', sans-serif;
    font-size: 14px;
    line-height: 1.5;
}

.text-info-about-gift span {
    font-size: 18px;
    font-weight: bold;
    color: #ff59cc;
}

.text-info-about-gift .wb-text {
    color: #fff;
    font-weight: bold;
    background: linear-gradient(to right, #ff59cc, #ff007c);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.big-reward {
    font-size: 48px;
    font-weight: bold;
    margin: 10px 0;
    color: #ff59cc;
}

.small-note {
    font-size: 12px;
    color: rgba(255, 255, 255, 0.7);
    margin-bottom: 20px;
}

.get-reward-button {
    background: linear-gradient(to right, #ff007c, #ff59cc);
    color: white;
    font-size: 16px;
    font-weight: bold;
    border: none;
    border-radius: 10px;
    padding: 10px 20px;
    cursor: pointer;
    transition: background 0.3s ease;
}

.get-reward-button:hover {
    background: linear-gradient(to right, #ff59cc, #ff007c);
}
