.all-settings-tasks {
    min-height: 100vh;
    overflow: auto;
}


.tasks-info {
    display: flex;
    gap: 30px;
    flex-direction: column;
}

.info-block {
    display: flex;
    color: white;
    width: 100%;
    border-bottom: 1px solid #262626;
    height: 110px;
    border-radius: 25px;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 5px;
}

.info-block p {
    z-index: 2;
    font-size: 10px;
    color: gray;
}

.info-block h2 {
    z-index: 2;
    font-size: 33px;
}

.icon-sky-pink {
    z-index: 1;
    position: absolute;
    height: 127px;
    top: -17px;
    width: 100%;
}

.card-partner {
    padding: 20px;
    color: white;
    height: 206px;
    border-top: 1px solid #262626;
    border-bottom: 1px solid #262626;
    border-radius: 25px;
}

.icon-gazprom {
    height: 68px;
    width: 50px;
}

.block-info-gift-card h2 {
    font-size: 20px;
}

.block-info-gift-card p {
    font-weight: 100;
    font-size: 12px;
    color: #696969;
}

.block-info {
    display: flex;
    justify-content: space-between;
    margin-bottom: 9px;
}

.pin-question-no-active {
    height: 12px;
    width: 12px;
    background: rgb(251, 100, 225);
    background: linear-gradient(342deg, rgba(251, 100, 225, 1) 0%, rgba(184, 26, 157, 1) 98%);
    padding: 5px;
    border-radius: 50%;
}

.get-bonus-card {
    padding-top: 10px;
    border-radius: 20px;
    padding-bottom: 1px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 133px;
    background-color: #212121;
}

.icon-and-info-block-bonus-card {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    gap: 15px;
    margin-bottom: 5px;
}

.get-bonus-card-text-info h2 {
    font-size: 19px;
    font-weight: 500;
    margin-bottom: 17px;
}

.get-bonus-card-text-info p {
    font-size: 10px;
    font-weight: 500;
    color: gray;
}

.button-get-card {
    width: 76%;
    height: 42px;
    border: none;
    color: white;
    background: linear-gradient(180deg, #e410ad 0%, #f5649f 100%);
    border-radius: 10px;
    font-size: 17px;
    font-weight: 600;
    margin-bottom: 2px;
}

.paragraph-ads-bank {
    font-size: 7px;
    color: gray;
}

.tasks-list-info {
    color: white;
    border-top: 1px solid #262626;
    border-radius: 25px;
    padding-top: 10px;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 14px;
}

.tasks-list-info h2 {
    margin-bottom: 10px;
}

.center-task {
    display: flex;
    justify-content: center;
    flex-direction: column;
    gap: 10px;
    margin-bottom: 20px;
}

.pin-question-no-active-task {
    height: 20px;
    width: 20px;
    border: 1px solid rgb(251, 100, 225);
    background: rgb(251, 100, 225);
    background: linear-gradient(342deg, rgba(251, 100, 225, 1) 0%, rgba(184, 26, 157, 1) 98%);
    padding: 5px;
    border-radius: 50%;
}

.task-disign {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 40px;
    width: 90%;
    border: 1px solid #262626;
    border-radius: 10px;
    flex-wrap: wrap;
    padding-left: 10px;
    padding-right: 10px;
    gap: 55px;
}

.info-task {
    margin-top: 4px;
    display: flex;
    flex-direction: column;
    gap: 1px;
}

.info-subscribe-to-channel {
    font-size: 10px;
}

.info-plus-spin {
    font-size: 9px;
    color: gray;
}

.text-and-icon-between {
    display: flex;
    flex-direction: row;
    gap: 13px;
}

.redirect-to-channel-for-sub {
    font-size: 12px;
    color: white;
    width: 80px;
    height: 27px;
    background: rgb(251, 100, 225);
    background: linear-gradient(342deg, rgba(251, 100, 225, 1) 0%, rgba(184, 26, 157, 1) 98%);
    border: none;
    border-radius: 5px;
}

.all-settings-tasks {
    min-height: 100vh;
    overflow: auto;
}

.background-color-gray {
    height: auto;
    min-height: 100vh;
    overflow-y: auto;
}

.tasks-list {
    max-height: 400px;
    overflow-y: auto;
    padding-right: 10px;
    scrollbar-width: thin;
}

.tasks-list::-webkit-scrollbar {
    width: 8px;
}

.tasks-list::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 4px;
}

.tasks-list::-webkit-scrollbar-thumb:hover {
    background: #555;
}
