body {
    margin: 0;
    font-family: 'Proxima Nova', Arial, sans-serif;
    font-weight: bold;
}

.footer {
    display: flex;
    position: sticky;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgba(78, 78, 78, 0.15);
    backdrop-filter: blur(10px);
    border-top: 1px solid #6C3661;
    padding: 10px 16px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
}

.footer-menu {
    display: flex;
    justify-content: space-around;
    width: 100%;
}

.menu-item {
    text-align: center;
    padding: 6px 0;
    width: 98px;
    border-radius: 12px;
    color: rgba(247, 247, 247, 0.4);
    text-decoration: none;
}

.menu-item.active {
    background-color: #EC11F1;
    color: white;
}

.icon-wrapper {
    height: 23px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.icon {
    width: 23px;
    height: 23px;
}

.menu-text {
    font-size: 12px;
    margin-top: 4px;
}
