.all-settings-friends {
    height: 100%;
}

.background-color-gray {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #151517;
    overflow-x: hidden;
}

.invite-section {
    position: relative;
    height: 195px;
    border: 1px solid #202023;
    border-radius: 0 0 1.5rem 1.5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    overflow: auto;
    color: white;
}

.background-image {
    position: absolute;
    inset: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.gradient-overlay {
    position: absolute;
    inset: 0;
    background: linear-gradient(to top, rgb(18, 18, 20) 10%, rgba(18, 18, 20, 0.8) 20%, rgba(18, 18, 20, 0) 50%);
}

.content-container {
    position: relative;
    z-index: 10;
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
}

.invite-title {
    font-size: 32.14px;
    font-weight: bold;
    line-height: 1.1;
}

.invite-description {
    font-size: 10.71px;
    line-height: 1.1;
    opacity: 0.5;
    margin-top: 0.25rem;
}

.invite-button {
    padding: 1px;
    background: linear-gradient(to top, transparent 70%, #f03ac2);
    border-radius: 12.17px;
    margin-top: 0.75rem;
    cursor: pointer;
    outline: none;
    border: none;
}

.button-content {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 0.25rem;
    background: linear-gradient(to bottom, #e204a9, #fe5fd6);
    width: 202px;
    height: 44px;
    border-radius: 12px;
    color: white;
    font-size: 17px;
    font-weight: bold;
    line-height: 1.1;
}

.link-container {
    color: white;
    display: flex;
    flex-direction: column;
    border: 1px solid #202023;
    border-radius: 15px;
    margin-top: 28px;
    padding: 12px 16px;
    gap: 2px;
}

.link-title {
    font-size: 19.2px;
    font-weight: bold;
    line-height: 23px;
}

.link-row {
    display: flex;
    align-items: center;
    gap: 4px;
}

.link-indicator {
    width: 1px;
    height: 16px;
    border-radius: 19px;
    background-color: rgb(225, 0, 168);
    box-shadow: 0 0 10.7px rgb(225, 0, 168);
}

.link-text {
    font-size: 13.69px;
    line-height: 17px;
    color: #f74dcc;
}

.link-icon {
    color: #f74dcc;
}

.stats-container {
    color: white;
    display: flex;
    flex-direction: column;
    border: 1px solid #202023;
    border-radius: 24px;
    margin-top: 28px;
    padding: 8px 16px;
    gap: 8px;
    height: 154px;
}

.stats-title {
    color: white;
    font-size: 20px;
    font-weight: bold;
    line-height: 24px;
}

.stats-items {
    display: flex;
    justify-content: center;
    gap: 8px;
}

.stats-item {
    color: white;
    position: relative;
    width: 100%;
}

.stats-box {
    color: white;
    border: 1px solid #202023;
    border-radius: 16px;
    width: 100%;
    height: 90px;
    position: relative;
    overflow: hidden;
}

.stats-icon-container {
    position: absolute;
    bottom: 10%;
    left: 50%;
    transform: translateX(-50%);
}

.stats-decoration {
    color: white;
    position: absolute;
    bottom: -20%;
    right: -18%;
}

.stats-value {
    color: white;
    position: absolute;
    top: 25%;
    left: 50%;
    transform: translateX(-50%);
    font-size: 24px;
    font-weight: bold;
    line-height: 36px;
}

.stats-label {
    color: white;
    position: absolute;
    bottom: 8px;
    width: 100%;
    text-align: center;
    font-size: 14px;
    font-weight: bold;
    line-height: 16px;
}

.background-color-gray {
    height: auto;
    min-height: 100vh;
    overflow-y: auto;
}

.stats-glow {
    color: white;
    position: absolute;
    bottom: 0;
    left: 50%;
    transform: translateX(-50%);
    width: 150px;
    height: 12px;
    background: #fc8dff;
    border-radius: 8px;
    opacity: 2;
    filter: blur(10px);
    margin-bottom: -16px;
}

.friends-container {
    color: white;
    border-top: 1px solid #202023;
    margin-top: 16px;
    border-radius: 16px 16px 0 0;
    display: flex;
    flex-direction: column;
    height: 250px;
    overflow-y: auto;
    padding-right: 10px;
    scrollbar-width: thin;
}

.friends-title {
    color: white;
    font-size: 20px;
    font-weight: bold;
    line-height: 24px;
    margin-left: 16px;
    margin-top: 12px;
}

.friends-content {
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 8px;
    gap: 8px;
}

.friends-svg-container {
    z-index: 0;
}

.friends-text {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    gap: 8px;
}

.friends-empty-title {
    font-size: 25px;
    font-weight: bold;
    line-height: 30px;
}

.friends-empty-description {
    font-size: 13px;
    line-height: 16px;
    color: rgba(141, 141, 141, 0.1);
}

.friends-svg-container {
    box-shadow: 0 0 25px 5px rgba(251, 72, 255, 0.3);
    border-radius: 50%;
}

.friends-svg {
    width: 79px;
    height: 79px;
}

.friends-plus-one {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 3px;
}

.friends-plus-one p {
    margin-left: 5px;
    margin-right: 5px;
}

.loading-bar-friends {
    position: absolute;
    top: 80%;
    right: 40%;
}