.toast-notif {
    display: flex;
    justify-content: center;
    align-items: center;
    position: fixed;
    bottom: 11%;
    left: 4%;
    z-index: 9999; 
    color: white;
    background-color: rgba(36, 36, 36, 0.993);
    width: 343px;
    height: 50px;
    border-radius: 10px;
    opacity: 0;
    animation: slideIn 0.3s forwards, slideOut 0.5s 2.5s forwards;
    text-align: center;
}


@keyframes slideIn {
    from {
        opacity: 0;
        transform: translateY(20px); 
    }
    to {
        opacity: 1;
        transform: translateY(0); 
    }
}

/* Анимация исчезновения */
@keyframes slideOut {
    from {
        opacity: 1;
        transform: translateY(0); 
    }
    to {
        opacity: 0;
        transform: translateY(20px); 
    }
}
