
.modal-how-play {
    z-index: 9999;
    display: none;
    width: 100%;
    height: 100%;
    background: linear-gradient(180deg, rgb(40, 23, 40) 0%, rgb(81, 26, 70) 100%) no-repeat fixed;
    position: fixed;
    top: 0;
    left: 0;
    overflow-y: hidden;
}


.view {
    display: block;
    position: absolute;
}

.logotip-wildbirries {
    width: 160px;
    height: 160px;
}

.icon-close-modal {
    height: 25px;
    width: 25px;
    position: absolute;
    top: 43px;
    right: 22px;
}

.high-bar-modal-hp {
    display: flex;
    flex-direction: row-reverse;
    justify-content: center;
}

.swiper {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    text-align: center;
    font-size: 27px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.icon-swiper {
    height: 15px;
    width: 15px;
}

.swiper-buttons {
    display: flex;
    flex: 0 1 auto;
    bottom: 0;
    left: 0;
    right: 0;
    background: radial-gradient(circle, rgba(252, 43, 218, 0.9) 0%, rgba(126, 0, 104, 0.7) 80%, rgba(97, 0, 80, 0.5) 100%);
    backdrop-filter: blur(2px);
    padding-bottom: 20px;
    padding-left: 50px;
    padding-right: 50px;
    padding-top: 10px;
}


.marker-text {
    background-color: #e204a9;
    padding: 1px;
}

.all-slides {
    color: white;
    display: flex;
    align-items: center;
}

.mini-text {
    color: #cecece;
    font-size: 10px;
    margin-top: 5px;
    margin-bottom: 5px;
}

.pink-text {
    color: #e204a9;
    font-size: 19px;
    margin-bottom: 3rem;
}

.slide-png {
    position: relative;
    text-align: center;
    width: 50px;
}

.swiper-buttons {
    position: absolute;
    z-index: 10000;
    display: flex;
    justify-content: space-between;
}

.style-swipper-btn {
    width: 65px;
    height: 65px;
    border: none;
    border-radius: 50%;
    background: linear-gradient(to bottom, #8a0067, #ff59cc);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.5), 0 0 15px rgba(255, 89, 204, 0.8); /* Тени для выделения */
    cursor: pointer;
    outline: none; 
    transition: box-shadow 0.3s ease; 
}

.style-swipper-btn:focus-visible {
    outline: none;
    box-shadow: 0 4px 15px rgba(255, 89, 204, 0.8), 0 0 20px rgba(255, 89, 204, 0.8); /* Пользовательский стиль при фокусе */
}

.style-swipper-btn:hover {
    box-shadow: 0 4px 20px rgba(255, 89, 204, 1), 0 0 25px rgba(255, 89, 204, 1);
}

.slide-2-style {
    font-size: 25px;
    margin-bottom: 2.5rem;
}

.slide-3-style {
    font-size: 21px;
    margin-bottom: 2.2rem;
}

.slide-4-style {
    font-size: 22px;
    margin-bottom: 1.6rem;
}

.pagination-view {
    color: white;
}