* {
    font-family: 'Roboto', sans-serif;
    margin: 0;
    padding: 0;
}


html, body {
    margin: 0;
    padding: 0;
    height: 100%;
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    overflow-x: hidden;

}

#root {
    height: 100%;
}

html {
    background-size: cover;
    background: linear-gradient(180deg, rgb(40, 23, 40) 0%, rgb(81, 26, 70) 100%) no-repeat fixed;
}

footer {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #333;
    color: white;
    height: 60px;
    margin-top: auto;
}

.all-roulete-settings {
    height: 91vh;
}

.header-info {
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin: 5px;
}

.button-size {
    border: none;
    height: 45px;
    width: 160px;
    border-radius: 10px;;
}

.color-button {
    color: white;
    font-weight: 600;
    font-size: 15px;
    background: linear-gradient(180deg, #e410ad 0%, #fc7fb5 100%);
}

.my-cupons {
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 5px;
    transition: transform 0.2s ease;
}

.how-to-play {
    display: flex;
    font-weight: 600;
    font-size: 15px;
    align-items: center;
    justify-content: center;
    gap: 5px;
    background-color: #151517;
    color: white;
    transition: transform 0.2s ease;
    -webkit-box-shadow: 5px 5px 19px -5px rgba(255, 255, 255, 0.04) inset;
    -moz-box-shadow: 5px 5px 19px -5px rgba(255, 255, 255, 0.04) inset;
    box-shadow: 5px 5px 19px -5px rgba(255, 255, 255, 0.04) inset;
}

.my-cupons:active, .how-to-play:active {
    transform: scale(0.95);
}

.icon-discount {
    width: 25px;
    height: 25px;
    padding: 4px;
    background-color: white;
    border-radius: 5px;
    color: #e520af;
}

.icon-question {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 14px;
    height: 14px;
    padding: 4px;
    font-weight: 600;
    font-size: 15px;
    background-color: white;
    border-radius: 50px;
}

.padding-two-icon-question {
    background-color: #e520af;
    padding: 5px;
    border-radius: 5px;
}

.gift-container {
    position: absolute;
    right: 16px; /* 4 rem в px */
    top: 45px;
    width: 75px;
    height: 96px;
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    cursor: pointer;
    z-index: 70;
    color: white;
}

.gift-container {
    opacity: 0;
    transform: scale(0.9);
    transition: opacity 0.4s ease, transform 0.4s ease;
  }
  
.gift-container, .fade-in {
    opacity: 1;
    transform: scale(1);
  }

  
.gift-image {
    position: absolute;
    top: 0;
    left: 0;
    width: 75px;
    height: 75px;
    object-fit: contain;
    z-index: 20;
    animation: myAnim 10s ease 3s infinite normal forwards;
}

@keyframes myAnim {
    0%,
    100% {
        transform: rotate(0deg);
        transform-origin: 50% 50%;
    }

    10% {
        transform: rotate(8deg);
    }

    20%,
    40%,
    60% {
        transform: rotate(-10deg);
    }

    30%,
    50%,
    70% {
        transform: rotate(10deg);
    }

    80% {
        transform: rotate(-8deg);
    }

    90% {
        transform: rotate(8deg);
    }
}

.gift-details {
    background: rgba(78, 78, 78, 0.15); /* Цвет с прозрачностью */
    backdrop-filter: blur(0px);
    border-top: 1px solid #5A3754;
    border-radius: 5px;
    height: 52px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-end;
    width: 56px;
}

.gift-title {
    font-size: 8px;
    font-weight: bold;
    line-height: 110%;
}

.gift-gradient {
    padding: 1px;
    background: linear-gradient(to top, transparent 70%, #F03AC2);
    border-radius: 100.17px;
}

.gift-content {
    display: flex;
    align-items: center;
    justify-content: center;
    background: linear-gradient(to bottom, #E204A9, #FE5FD6);
    width: 50px;
    height: 12px;
    border-radius: 234px;
    font-size: 8px;
}

.roulette-container {
    display: flex;
    justify-content: center;
    align-items: flex-end;
    padding: 1rem;
    margin-top: 2rem;
    margin-bottom: -0.1rem;
}

.roulette-outer {
    position: relative;
    width: 315px;
    height: 315px;
    transform: scale(0.9) translateY(0px);
}

.roulette-arrow {
    position: absolute;
    width: 24px;
    height: 18px;
    left: 50%;
    transform: translateX(-50%);
    transition: all 0.3s ease;
}

.arrow-top {
    z-index: 25;
    top: 7.5%;
}

.arrow-middle {
    top: 21%;
    z-index: 35;
}

.arrow-bottom {
    top: 34.6%;
    z-index: 45;
}

/* Общий стиль для кругов */
.circle {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    transition: transform 2.5s cubic-bezier(0.3, 0.1, 0.3, 0.9);
}

.circle-image {
    width: 100%;
    height: 100%;
    object-fit: contain;
    user-select: none;
}

.big-circle {
    width: 315px;
    height: 315px;
    z-index: 20;
    transform: translate(-50%, -50%) rotate(90deg);
}

.middle-circle {
    width: 314px;
    height: 314px;
    z-index: 30;
}

.small-circle {
    width: 219px;
    height: 219px;
    z-index: 40;
}

.roulette-button {
    width: 220px;
    position: absolute;
    top: 47px;
    left: 47px;
    z-index: 45;
}

.center-rotation-container {
    display: flex;
    justify-content: center;
}

.rotation-container {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 0.5rem;
    padding: 1rem;
    border-top: 1px solid #5a3754;
    background-color: rgba(78, 78, 78, 0.15);
    border-radius: 12px;
    width: 310px;
}

.icon-roulete-static {
    width: 42.59px;
    height: 35.58px;
    color: #ff27cc;
}

.rotation-text {
    font-weight: bold;
    font-size: 25px;
    line-height: 30px;
    color: white;
}

.spin-rewards {
    display: flex;
    gap: 16px;
    color: white;
    width: 341px;
    margin-bottom: 5px;
}

.reward-block {
    flex: 1;
    border-top: 1px solid #5a3754;
    backdrop-filter: blur(0px);
    background-color: rgba(78, 78, 78, 0.15);
    border-radius: 12px;
    padding: 12px 0;
    display: flex;
    flex-direction: column;
    gap: 8px;
    width: 50px;
}

.icon-text {
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
}

.icon {
    transform: scale(0.9);
}

.reward-text {
    font-size: 11.71px;
    font-weight: bold;
    line-height: 110%;
}

.button-container {
    display: flex;
    justify-content: center;
}

.gradient-button {
    background: linear-gradient(to top, rgba(240, 58, 194, 0.7), #f03ac2);
    padding: 4px 16px;
    border-radius: 234px;
    font-size: 13px;
    font-weight: bold;
    color: white;
    cursor: pointer;
    border: none;
    width: 118px;
    height: 30px;
}

.all-container-info-rotation {
    display: flex;
    gap: 5px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.global-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0);
    z-index: 999999;
    cursor: not-allowed;
}

