.all-settings-modal-how-play {
    height: 100%;
    pointer-events: none;
}

.coupon-info {
    height: 185px;
    border-bottom: 1px solid #262626;
    border-radius: 25px;
}

.info-block-coupon {
    display: flex;
    flex-direction: column;
    color: white;
    height: 100%;
    width: 92%;
    gap: 5px;
    padding-left: 15px;
    top: 27px;
    position: absolute;
    z-index: 100;
}

.shadow-icon-coupon {
    left: 18px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 33px;
    height: 33px;
    border-radius: 50%;
    overflow: hidden;
    background: linear-gradient(342deg, rgba(251, 100, 225, 1) 0%, rgba(184, 26, 157, 1) 98%);
    box-shadow: 0 0 15px rgba(255, 0, 255, 0.8);
}

.shadow-icon-coupon img {
    z-index: 1;
    object-fit: cover;
    width: 17px;
    height: 17px;
    filter: drop-shadow(0 0 15px rgba(255, 0, 255, 1));
}

.icon-coupon-roulete {
    width: 23px;
    height: 23px;
}


.button-get-activation-code {
    position: relative;
    pointer-events: auto;
    z-index: 2999999;
    width: 90%;
    height: 50px;
    border: none;
    border-radius: 10px;
    color: white;
    background: linear-gradient(180deg, #e410ad 0%, #f5649f 100%);
    font-size: 18px;
    font-weight: 600;
    left: 15px;
}

.info-block-coupon p {
    z-index: 2;
    font-size: 10px;
    color: gray;
    left: 18px;
    position: relative;
}

.info-amount-coupon h2 {
    z-index: 2;
    font-size: 30px;
}

.info-amount-coupon {
    z-index: 2;
    display: flex;
    gap: 5px;
    align-items: center;
    position: relative;
    left: 18px;
}

.icon-sky-pink-coupon {
    position: absolute;
    height: 127px;
    top: -5px;
    width: 100%;
}

.circle-icon-coupon {
    width: 70px;
    height: 70px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #410031;
    overflow: hidden;
    box-shadow: 0 0 25px rgba(117, 0, 88, 0.8), 0 0 50px rgba(117, 0, 88, 0.4);
}

.circle-icon-coupon img {
    width: 35px;
    height: 35px;
    object-fit: cover;
    filter: drop-shadow(0 0 15px rgba(255, 0, 255, 1));
    z-index: 1;
}

.split-coupon-info-and-text {
    display: flex;
    flex-direction: column;
    gap: 15px;
}

.list-cuppons {
    color: white;
    padding: 15px;
    border-top: 1px solid #262626;
    border-radius: 25px;
}

.info-coupon-text h2 {
    font-size: 21px;
}

.block-no-result-coupon-status {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.block-no-result-coupon-status p {
    font-size: 10px;
    color: gray;
}

.circle-icon-coupon {
    margin-bottom: 40px;
}