
.modal-win-five-tousend {
    z-index: 10000;
    position: fixed;
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: rgba(0, 0, 0, 0.5);
}

.block-win-five {
    color: white;
    height: auto;
    width: 77%;
    max-width: 400px;
    background: rgb(73, 0, 60);
    background: linear-gradient(175deg, rgba(73, 0, 60) 0%, rgba(55, 0, 45) 25%, rgba(0, 0, 0) 58%);
    border-radius: 20px;
    position: relative;
    text-align: center;
    padding: 20px;
    display: flex;
    flex-direction: column;
    align-items: center;
    box-shadow: 0px 10px 30px rgba(0, 0, 0, 0.5);
}

.main-text-win-five {
    font-size: 25px;
}

.main-block-text-five-win {
    margin-bottom: 1px;
}

.medium-text-win-five {
    font-size: 13px;
    margin-bottom: 25px;
}

.style-win-five {
    font-size: 50px;
    color: rgb(255, 255, 255);
    -webkit-text-stroke: 2px rgb(209, 54, 144);
    font-weight: 900;
}

.mini-text-rules {
    font-size: 9px;
    margin-bottom: 10px;
}

.get-my-prize-five-tousend {
    height: 40px;
    width: 200px;
    border: none;
    border-radius: 10px;
    font-size: 15px;
}